<template>
  <div class="asp">
    <div class="asp-nav"></div>
    <div class="asp-content">
      <el-form ref="form" style="padding:10px 0 0 0" :model="form" label-width="80px">
        <el-form-item label="">
          <el-input style="width:250px;" placeholder="30" v-model="form.role"></el-input>
        </el-form-item>
      </el-form>
      <div style="width:50%;margin:20px 0 0 20px">
        <tree-transfer
          :title="title"
          :from_data="fromData"
          :to_data="toData"
          :defaultProps="{label:'label'}"
          @addBtn="add"
          :default-checked-keys="[1]"
          @removeBtn="remove"
          :mode="mode"
          height="400px"
          filter
          openAll
        ></tree-transfer>
      </div>
      <div style="margin:0 0 0 50px">
        <el-button style="margin:30px 0 30px 0"> </el-button>
        <el-button @click="open1" type="primary"> </el-button>
      </div>
    </div>
  </div>
</template>
<script>
import treeTransfer from "el-tree-transfer";
export default {
  data() {
    return {
      form: {
        role: ""
      },
      title: ["", ""],
      mode: "transfer", // transfer addressList
      fromData: [
        // Array true element-ui treeidpid
        {
          id: "1",
          pid: 0, //pid"pid" false
          label: "",
          children: [
            {
              id: "1-2",
              pid: "1",
              label: "",
              children: [
                {
                  id: "1-2-1",
                  pid: "1-2",
                  children: [],
                  label: ""
                }
              ]
            }
          ]
        },
        {
          id: "1",
          pid: 0, //pid"pid" false
          label: "",
          children: [
            {
              id: "1-2",
              pid: "1",
              label: "",
              children: [
              ]
            }
          ]
        }
      ],
      toData: []
    };
  },
  methods: {
    open1() {
      this.$notify({
        title: "",
        message: "",
        type: "success"
      });
    },
    //transferaddressList

    changeMode() {
      if (this.mode == "transfer") {
        this.mode = "addressList";
      } else {
        this.mode = "transfer";
      }
    }, //
    add(fromData, toData, obj) {
      //transfer{keys,nodes,halfKeys,halfNodes}

      //addressList

      console.log("fromData:", fromData);

      console.log("toData:", toData);

      console.log("obj:", obj);
    }, //
    remove(fromData, toData, obj) {
      //transfer{keys,nodes,halfKeys,halfNodes}

      //addressList

      console.log("fromData:", fromData);

      console.log("toData:", toData);

      console.log("obj:", obj);
    }
  },
  components: { treeTransfer }, // 
  mounted: function() {},
  updated: function() {}
};
</script>
<style >
.asp {
  width: 100%;
  background: #f8f8f8;
  border: 1px solid #f8f8f8;
}
.el-textarea__inner {
  height: 100px;
}
.asp-nav {
  margin: 40px 0 0 20px;
  font-size: 18px;
  color: #2a2a2a;
  text-align: left;
}
.asp-content {
  width: 96%;
  margin: 20px 20px 20px 20px;
  background: #ffffff;
}
.asp-form {
  width: 100%;
  margin: 10px 0 0 0;
  border: 1px solid #f8f8f8;
}
.asp-table {
  width: 98%;
  background: #ffffff;
  margin: 10px 0 0 1%;
}
.el-pagination {
  margin: 20px 0 0 150px;
  padding: 0 0 20px 0;
}
.el-checkbox__label {
  font-size: 10px;
}
.el-checkbox {
  margin: 10px 0 0 20px;
}
.el-tree-node__content {
  display: flex;
  align-items: center;
  height: 26px;
  cursor: pointer;
}
.el-checkbox__inner {
  display: inline-block;
  position: relative;
  border: 1px solid #dcdfe6;
  border-radius: 2px;
  box-sizing: border-box;
  width: 14px;
  height: 14px;
  background-color: #fff;
  z-index: 1;
  transition: border-color 0.25s cubic-bezier(0.71, -0.46, 0.29, 1.46),
    background-color 0.25s cubic-bezier(0.71, -0.46, 0.29, 1.46);
  margin: 0 10px 8px -20px;
}
</style>


